 .la-ball-pulse-sync,
 .la-ball-pulse-sync>div {
   position: relative;
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   box-sizing: border-box;
 }
 
 .threadLoading.la-ball-pulse-sync{
   opacity: 1;
   display: block;
 }
 
 .la-ball-pulse-sync {
   display: none;
   font-size: 0;
   color: #fff;
   opacity: 0;
   transition: 0.3s ease-in-out;
 }
 
 .la-ball-pulse-sync.la-dark {
   color: #333;
 }
 
 .la-ball-pulse-sync>div {
   display: inline-block;
   float: none;
   background-color: currentColor;
   border: 0 solid currentColor;
 }
 
 .la-ball-pulse-sync {
   height: 18px;
 }
 
 .la-ball-pulse-sync>div {
   width: 10px;
   height: 10px;
   margin: 4px;
   border-radius: 100%;
   -webkit-animation: ball-pulse-sync .8s infinite ease-in-out;
   -moz-animation: ball-pulse-sync .8s infinite ease-in-out;
   -o-animation: ball-pulse-sync .8s infinite ease-in-out;
   animation: ball-pulse-sync .8s infinite ease-in-out;
 }
 
 .la-ball-pulse-sync>div:nth-child(1) {
   -webkit-animation-delay: -.21s;
   -moz-animation-delay: -.21s;
   -o-animation-delay: -.21s;
   animation-delay: -.21s;
 }
 
 .la-ball-pulse-sync>div:nth-child(2) {
   -webkit-animation-delay: -.14s;
   -moz-animation-delay: -.14s;
   -o-animation-delay: -.14s;
   animation-delay: -.14s;
 }
 
 .la-ball-pulse-sync>div:nth-child(3) {
   -webkit-animation-delay: -.07s;
   -moz-animation-delay: -.07s;
   -o-animation-delay: -.07s;
   animation-delay: -.07s;
 }
 
 .la-ball-pulse-sync>div:nth-child(4) {
   -webkit-animation-delay: 0s;
   -moz-animation-delay: 0s;
   -o-animation-delay: 0s;
   animation-delay: 0s;
 }
 
 .la-ball-pulse-sync.la-sm {
   width: 26px;
   height: 8px;
 }
 
 .la-ball-pulse-sync.la-sm>div {
   width: 4px;
   height: 4px;
   margin: 2px;
 }
 
 .la-ball-pulse-sync.la-2x {
   width: 108px;
   height: 36px;
 }
 
 .la-ball-pulse-sync.la-2x>div {
   width: 20px;
   height: 20px;
   margin: 8px;
 }
 
 .la-ball-pulse-sync.la-3x {
   width: 162px;
   height: 54px;
 }
 
 .la-ball-pulse-sync.la-3x>div {
   width: 30px;
   height: 30px;
   margin: 12px;
 }
 
 /*
   * Animation
   */
 @-webkit-keyframes ball-pulse-sync {
   33% {
     -webkit-transform: translateY(100%);
     transform: translateY(100%);
   }
 
   66% {
     -webkit-transform: translateY(-100%);
     transform: translateY(-100%);
   }
 
   100% {
     -webkit-transform: translateY(0);
     transform: translateY(0);
   }
 }
 
 @-moz-keyframes ball-pulse-sync {
   33% {
     -moz-transform: translateY(100%);
     transform: translateY(100%);
   }
 
   66% {
     -moz-transform: translateY(-100%);
     transform: translateY(-100%);
   }
 
   100% {
     -moz-transform: translateY(0);
     transform: translateY(0);
   }
 }
 
 @-o-keyframes ball-pulse-sync {
   33% {
     -o-transform: translateY(100%);
     transform: translateY(100%);
   }
 
   66% {
     -o-transform: translateY(-100%);
     transform: translateY(-100%);
   }
 
   100% {
     -o-transform: translateY(0);
     transform: translateY(0);
   }
 }
 
 @keyframes ball-pulse-sync {
   33% {
     -webkit-transform: translateY(100%);
     -moz-transform: translateY(100%);
     -o-transform: translateY(100%);
     transform: translateY(100%);
   }
 
   66% {
     -webkit-transform: translateY(-100%);
     -moz-transform: translateY(-100%);
     -o-transform: translateY(-100%);
     transform: translateY(-100%);
   }
 
   100% {
     -webkit-transform: translateY(0);
     -moz-transform: translateY(0);
     -o-transform: translateY(0);
     transform: translateY(0);
   }
 }